// services/company.service.ts
import api from "./api";
import { BranchFormData } from "@/components/Company/BranchFormModal";
import {  CategoryFormData } from "@/components/Company/CategoryFormModal";
import {  CategoryTypeFormDataType } from "@/components/Company/CategoryTypeFormModal";
import {  ProductFormData } from "@/components/Company/ProductFormModal";

export const CompanyService = {
  // GET ALL
  getAll() {
    return api.get("/SetUp/companies");
  },

  // GET BY ID
  getById(id: string) {
    return api.get(`/SetUp/get-company/${id}`);
  },


      // GET ALL
  getAllBranches() {
    return api.get("/SetUp/branches");
  },




  // CREATE (FromForm)
  create(data: FormData) {
    return api.post("/SetUp/create-company", data, {
      headers: {
        "Content-Type": "multipart/form-data",
      },
    });
  },

  // âœ… UPDATE COMPANY (FromForm + route id)
  updateCompany(id: string, data: FormData) {
    return api.put(`/SetUp/edit-company/${id}`, data, {
      headers: {
        "Content-Type": "multipart/form-data",
      },
    });
  },

  // DELETE
  delete(id: string) {
    return api.delete(`/SetUp/delete-company/${id}`);
  },



  // GET BY ID
  getByIdBranch(id: string) {
    return api.get(`/SetUp/get-branch/${id}`);
  },

  // CREATE BRANCH (JSON)
  createBranch(data: BranchFormData) {
    return api.post("/SetUp/create-branch", data);
  },

  
    // UPDATE BRANCH (JSON)
  updateBranch(id: string, data: BranchFormData) {
    return api.put(`/SetUp/edit-branch/${id}`, data);
  },



  



  // DELETE
  deleteBranch(id: string) {
    return api.delete(`/SetUp/delete-branch/${id}`);
  },




        // GET ALL
  getAllCategory() {
    return api.get("/SetUp/categories");
  },

  // CREATE BRANCH (JSON)
  createCategory(data: CategoryFormData) {
    return api.post("/SetUp/create-category", data);
  },


  // CREATE BRANCH (JSON)
  createCategoryType(data: CategoryTypeFormDataType) {
    return api.post("/Product/create-CategoryType", data);
  },



    // CREATE BRANCH (JSON)
  CreateProductAsync(data: ProductFormData) {
    return api.post("/Product/create-product", data);
  },



      // UPDATE BRANCH (JSON)
  updateCategoryType(id: string, data: CategoryTypeFormDataType) {
    return api.put(`/Product/edit-CategoryType/${id}`, data);
  },


      getAllCategoryTypes() {
    return api.get("/Product/category-list");
  },
   

        getAllProducts() {
    return api.get("/Product/product-list");
  },
   


      // UPDATE CATEGORY (JSON)
  updateCategory(id: string, data: CategoryFormData) {
    return api.put(`/SetUp/edit-category/${id}`, data);
  },

  
  // DELETE
  deleteCategory(id: string) {
    return api.delete(`/SetUp/delete-category/${id}`);
  },



};
